,WAAA,CAEA,0CACE,YAAA,CAGF,yCACE,YAAA,CC1FN,kBACE,cAAA,CACA,QAAA,CACA,UAAA,CACA,WAAA,CACA,YAAA,CACA,sBAAA,CACA,kBAAA,CAEA,yCACE,cAAA,CACA,kBAAA,CAGF,2CACE,iBAAA,CACA,qBAAA,CACA,uBAAA,CACA,UAAA,CACA,WAAA,CCnBJ,uBACE,cAAA,CACA,sBAAA,CACA,8DAAA,CAGA,2BAAA,CAEA,6CACE,iBAAA,CACA,2BAAA,CAGF,qCACE,YAAA,CACA,kBAAA,CACA,kBAAA,CACA,YVLkB,CUMlB,UAAA,CACA,yBAAA,CACA,qBAAA,CACA,cAAA,CAEA,6CACE,SAAA,CACA,UAAA,CACA,qBAAA,CACA,UAAA,CACA,uBAAA,CAGF,wDACE,eAAA,CAEA,+DACE,cAAA,CACA,uBAAA,CAGF,mEACE,WAAA,CACA,eAAA,CACA,cAAA,CACA,qCTDO,CU1Cf,sBACE,iBAAA,CACA,UAAA,CACA,sBAAA,CACA,8DAAA,CAGA,SAAA,CACA,UAAA,CAEA,2CACE,SAAA,CACA,SAAA,CAGF,mCACE,YAAA,CACA,kBAAA,CACA,kBAAA,CACA,YXNiB,CWOjB,UAAA,CACA,yBAAA,CACA,qBAAA,CACA,cAAA,CACA,oBAAA,CAEA,uCACE,4BAAA,CAGF,2CACE,SAAA,CACA,UAAA,CACA,qBAAA,CACA,UAAA,CACA,uBAAA,CAIA,4DACE,UAAA,CACA,cAAA,CACA,uBAAA,CAAA","sourcesContent":["/**\r\n * Styles shared between mobile and desktop\r\n */\r\n\r\n.logo {\r\n  fill: white;\r\n}\r\n\r\nbody {\r\n  margin: 0;\r\n  background-color: $LANDING_PAGE_BACKGROUND_COLOR;\r\n  font-family: 'Sprat Type';\r\n}\r\n\r\na {\r\n  color: white;\r\n}","/**\r\n * Constants for stylesheet\r\n */\r\n\r\n// sizing\r\n$X_MARGIN: 10vw;\r\n$BOTTOM_MARGIN: 10vh;\r\n$TOP_MARGIN: 36px;\r\n$DESKTOP_WIDTH: 1240px;\r\n/**\r\n * NOTE: this is completely arbitrary and loosely based upon values at\r\n * https://www.kylejlarson.com/blog/ipad-screen-size-guide-web-design-tips/.\r\n * - Desktop readjusted to 1240 since the navbar starts looking a bit squished\r\n * - Tablet set to 720 to be below minimum iPad browser width\r\n */\r\n$TABLET_WIDTH: 672px;\r\n$LINE_ELEMENT_HEIGHT: 142px;\r\n$NAV_ELEMENT_HEIGHT: 126px;\r\n\r\n// letter spacing\r\n$HEADER_TRACKING: -15px;\r\n$TITLE_TRACKING: -5px;\r\n$SUBHEAD_TRACKING: -2px;\r\n\r\n// Background color\r\n$LANDING_PAGE_BACKGROUND_COLOR: #141414;","/**\r\n * Import all the fonts used in this project.\r\n */\r\n\r\n@import url('https://fonts.googleapis.com/css2?family=Libre+Franklin:wght@200;300;400&family=Overpass+Mono:wght@300;400;600;700&display=swap');\r\n@import url('https://use.typekit.net/zhb6cru.css');\r\n@import url('https://fonts.googleapis.com/css2?family=Overpass:ital@0;1&display=swap');\r\n\r\n@font-face {\r\n    font-family: \"Sprat Type\";\r\n    src: url(\"../../assets/fonts/sprat-type/Sprat-Regular.otf\");\r\n    font-weight: normal;\r\n    font-style: normal;\r\n}\r\n\r\n@font-face {\r\n    font-family: \"Sprat Type\";\r\n    src: url(\"../../assets/fonts/sprat-type/Sprat-RegularBold.otf\");\r\n    font-weight: bold;\r\n}\r\n\r\n@font-face {\r\n    font-family: \"Sprat Type\";\r\n    src: url(\"../../assets/fonts/sprat-type/Sprat-RegularLight.otf\");\r\n    font-weight: 300;\r\n}\r\n\r\n@font-face {\r\n    font-family: \"Sprat Type\";\r\n    src: url(\"../../assets/fonts/sprat-type/Sprat-RegularThin.otf\");\r\n    font-weight: 100;\r\n}\r\n\r\n@font-face {\r\n    font-family: 'Sprat Type Condensed';\r\n    src: url('../../assets/fonts/sprat-type/Sprat-CondensedLight.otf');\r\n    font-weight: 100;\r\n}\r\n\r\n@font-face {\r\n    font-family: 'Sprat Type Condensed';\r\n    src: url('../../assets/fonts/sprat-type/Sprat-CondensedRegular.otf');\r\n    font-weight: normal;\r\n}\r\n\r\n// font families\r\n$FONT_SUBHEAD: 'Overpass Mono', monospace;\r\n$FONT_BODY: 'franklin-gothic-urw', sans-serif;;\r\n\r\n// font sizes\r\n$FONT_SIZE_NAV: 30px; // changed from 38px\r\n$FONT_SIZE_LINK: 24px;\r\n$FONT_SIZE_LINE: 114px; // changed from 144px\r\n$FONT_SIZE_DESIGNER: 36px;\r\n$FONT_SIZE_TITLE: 48px;\r\n$FONT_SIZE_BODY: 18px;\r\n$FONT_SIZE_LABEL: 30px;\r\n\r\n// font adjustments and weights\r\n$FONT_SPRAT_STRETCH: scaleY(1.2);\r\n$FONT_SPRAT_WEIGHT: 300;\r\n$FONT_TITLE_WEIGHT: 400;\r\n$FONT_LINK_WEIGHT: 400;\r\n$FONT_LABEL_WEIGHT: 200;","/**\r\n * Compiles all the styles together\r\n */\r\n\r\n// Shared all screens\r\n@import './constants.scss';\r\n@import './common.scss';\r\n@import './fonts.scss';\r\n\r\n// Desktop styles\r\n@media (min-width: $DESKTOP_WIDTH) {\r\n  @import './desktop/index.scss';\r\n  @import './desktop/components/navbar.scss';\r\n  @import './desktop/components/landing_page.scss';\r\n  @import './desktop/components/desktop_side_nav.scss';\r\n}\r\n\r\n// Mobile styles\r\n@media (max-width: $TABLET_WIDTH - 1) {\r\n  @import './mobile/index.scss';\r\n  @import './mobile/components/landing_page.scss';\r\n  @import './mobile/components/mobile_open_menu.scss';\r\n  @import './mobile/components/mobile_menu_line_list.scss';\r\n  @import './mobile/components/mobile_menu_nav_list.scss';\r\n}\r\n\r\n// Tablet styles\r\n@media (min-width: $TABLET_WIDTH) and (max-width: $DESKTOP_WIDTH - 1) {\r\n  // Landscape orientation: slight adjustments from desktop\r\n  @media (orientation: landscape) {\r\n    @import './desktop/index.scss';\r\n    @import './tablet/components/navbar.scss';\r\n    @import './tablet/components/landing_page.scss';\r\n  }\r\n  // Portrait orientation: copy mobile\r\n  @media (orientation: portrait) {\r\n    @import './mobile/index.scss';\r\n    @import './mobile/components/landing_page.scss';\r\n    @import './mobile/components/mobile_open_menu.scss';\r\n    @import './mobile/components/mobile_menu_line_list.scss';\r\n    @import './mobile/components/mobile_menu_nav_list.scss';\r\n  }\r\n}","/**\r\n * Styles for the main site\r\n */\r\n\r\nbody {\r\n  color: white;\r\n  height: 100vh;\r\n  width: 100%;\r\n}\r\n\r\n.mobile {\r\n  display: none;\r\n}\r\n\r\n/* Display font */\r\nh1{\r\n  font-family: 'Sprat Type Condensed';\r\n  font-weight: 100;\r\n\tfont-size: 4em;\r\n\tletter-spacing: -.11em;\r\n\tword-spacing: .1em;\r\n\tmargin-bottom: 0;\r\n\ttext-transform: uppercase;\r\n}\r\n\r\n/* Subheader 1 */\r\nh2 {\r\n  font-family: 'Sprat Type Condensed';\r\n  font-weight: normal;\r\n\tfont-size: 1.5em;\r\n\tletter-spacing: -.01em;\r\n\tmargin-top: 0;\r\n\tmargin-bottom: 1em;\r\n\ttext-transform: uppercase;\r\n}\r\n\r\n/* Subheader 2 */\r\nh3 {\r\n\tfont-family: 'Overpass Mono', monospace;\r\n\tfont-weight: 400;\r\n\tfont-size: 1em;\r\n\tletter-spacing: -0.05em;\r\n\tmargin-bottom: 0;\r\n\tmargin-top: 0;\r\n\ttext-transform: uppercase;\r\n}\r\n\r\n/* Body text */\r\np {\r\n\tfont-family: 'franklin-gothic-urw', sans-serif;\r\n\tfont-weight: 400;\r\n\tfont-style: normal;\r\n\tfont-size: 1em;\r\n\tmargin-top: 0.5em;\r\n}","/**\r\n * Landing page navbar\r\n * https://github.com/EthanNakache/Sprat-type\r\n */\r\n\r\n@keyframes underline-animation {\r\n  from {\r\n    width: 0px;\r\n  } to {\r\n    width: 24px;\r\n  }\r\n}\r\n\r\n@keyframes underline-circle-left-animation {\r\n  from {\r\n    transform: translateX(0);\r\n  } to {\r\n    transform: translateX(-12px);\r\n  }\r\n}\r\n\r\n@keyframes underline-circle-right-animation {\r\n  from {\r\n    transform: translateX(0);\r\n  } to {\r\n    transform: translateX(12px);\r\n  }\r\n}\r\n\r\n.navbar {\r\n  width: 100vw - $X_MARGIN;\r\n  display: flex;\r\n  flex-direction: row;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  // Put this navbar at the bottom\r\n  position: fixed;\r\n  bottom: $BOTTOM_MARGIN*.8;\r\n\r\n  .navBall {\r\n    margin: auto;\r\n    display: block;\r\n    transform: scale(.9, .8);\r\n    opacity: 0;\r\n    transition: .5s;\r\n  }\r\n\r\n  .underline-circle {\r\n    position: absolute;\r\n    width: 8px;\r\n    height: 8px;\r\n    background-color: white;\r\n    border-radius: 100%;\r\n  }\r\n\r\n  .underline {\r\n    position: absolute;\r\n    width: 0px;\r\n    height: 1px;\r\n    background-color: white;\r\n  }\r\n\r\n  .navbar-item {\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n    font-size: $FONT_SIZE_NAV;\r\n    cursor: pointer;\r\n    transform: $FONT_SPRAT_STRETCH;\r\n    font-weight: $FONT_SPRAT_WEIGHT;\r\n    transition: .5s;\r\n    border-bottom: 2px solid;\r\n    border-color: rgba(255, 255, 255, 0);\r\n    padding-bottom: $BOTTOM_MARGIN*0.05;\r\n    transition: .5s;\r\n\r\n    .underline-animation {\r\n      margin-top: 4px;\r\n      display: flex;\r\n      align-items: center;\r\n      justify-content: center;\r\n      width: 100%;\r\n      height: 12px;\r\n      opacity: 0;\r\n      transition: 0.5s;\r\n    }\r\n\r\n    &.selected {\r\n      .navBall {\r\n        opacity: 1;\r\n      }\r\n\r\n      .underline-animation {\r\n        opacity: 1;\r\n\r\n        .left {\r\n          animation: 1s ease-in-out underline-circle-left-animation forwards;\r\n        }\r\n\r\n        .right {\r\n          animation: 1s ease-in-out underline-circle-right-animation forwards;\r\n        }\r\n\r\n        .underline {\r\n          animation: 1s ease-in-out underline-animation forwards;\r\n        }\r\n      }\r\n    }\r\n  }\r\n}\r\n","/**\r\n * First page the user sees, the landing page.\r\n *\r\n * Contains a 3D asset in the back.\r\n */\r\n\r\n $CIRCLE_RAD : 12px;\r\n $OUTER : $X_MARGIN/8;\r\n $LOWER : $BOTTOM_MARGIN/4;\r\n $UPPER : $BOTTOM_MARGIN*2.2;\r\n\r\n.landing-page-background {\r\n  background-color: black;\r\n  width: 100%;\r\n  height: 100vh;\r\n\r\n\r\n  #landing-page-cube {\r\n    width: 100%;\r\n    height: 100vh;\r\n  }\r\n}\r\n\r\n.landing-page {\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  justify-content: center;\r\n  overflow: hidden;\r\n\r\n  .faded {\r\n    opacity: 0;\r\n    transition: .2s;\r\n  }\r\n\r\n  .notFaded {\r\n    opacity: 1;\r\n    transition: .3s;\r\n  }\r\n\r\n  #main-screen {\r\n    flex-direction: row;\r\n\r\n    #curr-line {\r\n      position: absolute;\r\n      left: $X_MARGIN/2;\r\n      bottom: 40%;\r\n      display: block;\r\n  \r\n      #line-name {\r\n        font-size: $FONT_SIZE_LINE;\r\n        text-transform: uppercase;\r\n        transform: $FONT_SPRAT_STRETCH;\r\n        font-weight: $FONT_SPRAT_WEIGHT;\r\n        letter-spacing: $HEADER_TRACKING;\r\n      }\r\n  \r\n      #designers-name {\r\n        font-size: $FONT_SIZE_DESIGNER;\r\n        font-family: $FONT_SUBHEAD;\r\n        text-transform: uppercase;\r\n        letter-spacing: $SUBHEAD_TRACKING;\r\n      }\r\n    }  \r\n\r\n    #more-info {\r\n      font-family: $FONT_BODY;\r\n      text-transform: uppercase;\r\n      font-size: $FONT_SIZE_LABEL;\r\n      position: absolute;\r\n      right: $X_MARGIN/2;\r\n      bottom: 37vh;\r\n      text-decoration: underline;\r\n      font-weight: $FONT_LABEL_WEIGHT;\r\n      text-underline-offset: 6px;\r\n    }\r\n\r\n    #see-more-line {\r\n      position: absolute;\r\n      right: $OUTER;\r\n      bottom: 35vh;\r\n      width: 60vw;\r\n      border-top: 1px solid white;\r\n    }\r\n\r\n    #see-more-dot {\r\n      bottom: 35vh;\r\n      right: 60vw + $OUTER;\r\n    }\r\n\r\n    .vertical-line {\r\n      height: 100vh;\r\n      top: 0;\r\n      position: absolute;\r\n      border-left: 1px solid white;\r\n      border-right: 1px solid white;\r\n      pointer-events: none;\r\n    }\r\n\r\n    #outer-lines {\r\n      left: $OUTER;\r\n      right: $OUTER;\r\n    }\r\n\r\n    #inner-lines {\r\n      left: $X_MARGIN/4;\r\n      right: $X_MARGIN/4;\r\n    }\r\n\r\n    .lower {\r\n      bottom: $LOWER;\r\n    }\r\n\r\n    .upper {\r\n      bottom: $UPPER;\r\n    }\r\n\r\n    .horizontal-line {\r\n      left: 0;\r\n      width: 100vw;\r\n      position: absolute;\r\n      border-bottom: 1px solid white;\r\n    }\r\n\r\n    .dot {\r\n      width: $CIRCLE_RAD;\r\n      height: $CIRCLE_RAD;\r\n      background-color: white;\r\n      border-radius: 100%;\r\n      position: absolute;\r\n      margin-bottom: -$CIRCLE_RAD/2;\r\n    }\r\n\r\n    .left-dot {\r\n      margin-left: -$CIRCLE_RAD/2;\r\n      left: $OUTER;\r\n    }\r\n\r\n    .right-dot {\r\n      margin-right: -$CIRCLE_RAD/2;\r\n      right: $OUTER;\r\n    }\r\n\r\n  }\r\n\r\n  .title-theme {\r\n    position: absolute;\r\n    top: $TOP_MARGIN;\r\n    font-size: $FONT_SIZE_TITLE;\r\n    transform: $FONT_SPRAT_STRETCH;\r\n    font-weight: $FONT_TITLE_WEIGHT;\r\n    letter-spacing: $TITLE_TRACKING;\r\n  }\r\n\r\n  .logo {\r\n    position: absolute;\r\n\r\n    // Gonna be totally honest I don't think this is _quite_ accurate to get 21px under the logo\r\n    top: $TOP_MARGIN;\r\n    right: $X_MARGIN/2;\r\n    width: 61px;\r\n    height: 80px;\r\n  }\r\n}","/**\r\n * Navigation sidebar on desktop\r\n */\r\n\r\n.links {\r\n  position: absolute;\r\n  top: $TOP_MARGIN;\r\n  left: $X_MARGIN/2; // an aprox guess to get the actual links aligned with the rest of the page\r\n\r\n  .link {\r\n    font-family: $FONT_BODY;\r\n    font-size: $FONT_SIZE_LINK;\r\n    text-transform: uppercase;\r\n    font-weight: $FONT_LINK_WEIGHT;\r\n    display: block;\r\n    color: white;\r\n    text-decoration: none;\r\n    padding-bottom: 8px;\r\n  }\r\n}\r\n","/**\r\n * Mobile styles in general for the site\r\n */\r\n\r\nhtml {\r\n  height: 100%;\r\n}\r\n\r\nbody {\r\n  color: white;\r\n  height: 100%;\r\n  width: 100%;\r\n\r\n  #root {\r\n    height: 100%;\r\n  }\r\n}\r\n\r\n.desktop {\r\n  display: none;\r\n}","\r\n// TODO: unsure why this font is so much smaller than figma\r\n$FONT_SIZE_TITLE: 48px;\r\n\r\n/**\r\n * 3D model in the landing page.\r\n */\r\n.landing-page-background {\r\n  z-index: -1;\r\n  position: fixed;\r\n  background-color: black;\r\n  width: 100%;\r\n  height: 100%;\r\n  transition-duration: 1s;\r\n  transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);\r\n\r\n  #landing-page-cube {\r\n    width: 100%;\r\n    height: 100%;\r\n  }\r\n\r\n  &.mobile-line-menu-open {\r\n    top: 0px;\r\n    height: 200px;\r\n  }\r\n\r\n  &.mobile-nav-menu-open {\r\n    bottom: 0px;\r\n    height: 200px;\r\n  }\r\n}\r\n\r\n.landing-page {\r\n  position: relative;\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  justify-content: center;\r\n  height: 100%;\r\n  width: 100%;\r\n\r\n  &.mobile-line-menu-open {\r\n    position: static;\r\n  }\r\n\r\n  .title-theme {\r\n    position: fixed;\r\n    font-size: $FONT_SIZE_TITLE;\r\n    // Vertically stretch the font\r\n    transform: scale(1, 1.5);\r\n    transition-duration: 1s;\r\n    transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);\r\n\r\n    &.mobile-line-menu-open {\r\n      // Title becomes a bit smaller on the mobile menu open\r\n      font-size: $FONT_SIZE_TITLE * 0.8;\r\n      top: 90px;\r\n    }\r\n\r\n    &.mobile-nav-menu-open {\r\n      // Title becomes a bit smaller on the mobile menu open\r\n      font-size: $FONT_SIZE_TITLE * 0.8;\r\n      bottom: 60px;\r\n    }\r\n  }\r\n\r\n  // Hide desktop elements\r\n  .link {\r\n    display: none;\r\n  }\r\n\r\n  .lineLabel {\r\n    display: none;\r\n  }\r\n\r\n  #label {\r\n    display: none;\r\n  }\r\n\r\n  #curr_line {\r\n    display: none;\r\n  }\r\n\r\n  .logo {\r\n    position: fixed;\r\n    bottom: 21px;\r\n    width: 69px;\r\n    height: 90px;\r\n\r\n    &.mobile-line-menu-open {\r\n      display: none;\r\n    }\r\n\r\n    &.mobile-nav-menu-open {\r\n      display: none;\r\n    }\r\n  }\r\n}\r\n","/**\r\n * Component on mobile that opens the menu\r\n */\r\n\r\n#mobile-open-menu {\r\n  position: fixed;\r\n  top: 34px;\r\n  width: 37px;\r\n  height: 37px;\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n\r\n  #mobile-open-menu-sign {\r\n    font-size: 40px;\r\n    margin-bottom: 10px;\r\n  }\r\n\r\n  #mobile-open-menu-border {\r\n    position: absolute;\r\n    border: 1px solid white;\r\n    transform: rotate(45deg);\r\n    width: 100%;\r\n    height: 100%;\r\n  }\r\n}","/**\r\n * Styles for the menu that shows all the lines in the show\r\n */\r\n\r\n#mobile-menu-line-list {\r\n  position: fixed;\r\n  transition-duration: 1s;\r\n  transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);\r\n\r\n  // Initially, the line menu is hidden and below\r\n  transform: translateY(-100%);\r\n\r\n  &.mobile-line-menu-open {\r\n    position: absolute;\r\n    transform: translateY($LINE_ELEMENT_HEIGHT*7);\r\n  }\r\n\r\n  .line-element {\r\n    display: flex;\r\n    flex-direction: row;\r\n    align-items: center;\r\n    height: $LINE_ELEMENT_HEIGHT;\r\n    width: 100%;\r\n    border-top: 1px solid white;\r\n    background-color: black;\r\n    cursor: pointer;\r\n\r\n    .bullet {\r\n      width: 8px;\r\n      height: 8px;\r\n      background-color: white;\r\n      margin: 9px;\r\n      transform: rotate(45deg);\r\n    }\r\n\r\n    .line-element-body {\r\n      margin-top: 32px;\r\n\r\n      .title {\r\n        font-size: 36px;\r\n        transform: scale(1, 1.2);\r\n      }\r\n\r\n      .designers {\r\n        height: 16px;\r\n        margin-top: 16px;\r\n        font-size: 16px;\r\n        font-family: $FONT_SUBHEAD;\r\n      }\r\n    }\r\n  }\r\n}","/**\r\n * Styles for the menu that shows all the nav links\r\n */\r\n\r\n#mobile-menu-nav-list {\r\n  position: relative;\r\n  width: 100%;\r\n  transition-duration: 1s;\r\n  transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);\r\n\r\n  // Initially, the line menu is hidden and below\r\n  opacity: 0;\r\n  z-index: -2;\r\n\r\n  &.mobile-nav-menu-open {\r\n    opacity: 1;\r\n    z-index: 2;\r\n  }\r\n\r\n  .nav-element {\r\n    display: flex;\r\n    flex-direction: row;\r\n    align-items: center;\r\n    height: $NAV_ELEMENT_HEIGHT;\r\n    width: 100%;\r\n    border-top: 1px solid white;\r\n    background-color: black;\r\n    cursor: pointer;\r\n    text-decoration: none;\r\n\r\n    &.end {\r\n      border-bottom: 1px solid white;\r\n    }\r\n\r\n    .bullet {\r\n      width: 8px;\r\n      height: 8px;\r\n      background-color: white;\r\n      margin: 9px;\r\n      transform: rotate(45deg);\r\n    }\r\n\r\n    .nav-element-body {\r\n      .title {\r\n        color: white;\r\n        font-size: 36px;\r\n        transform: scale(1, 1.2);\r\n      }\r\n    }\r\n  }\r\n}\r\n","/**\r\n * Landing page navbar\r\n * https://github.com/EthanNakache/Sprat-type\r\n */\r\n\r\n.navbar {\r\n  width: 80%;\r\n  display: flex;\r\n  flex-direction: row;\r\n  justify-content: space-around;\r\n  align-items: center;\r\n  padding: 12px 0 12px 0;\r\n\r\n  // Put this navbar at the bottom\r\n  position: fixed;\r\n  bottom: 20%;\r\n\r\n  .diamond {\r\n    margin: auto;\r\n    width: 8.8px;\r\n    height: 8.8px;\r\n    background-color: white;\r\n    transform: rotate(45deg);\r\n    margin-bottom: 10px;\r\n    visibility: hidden;\r\n\r\n    &.selected {\r\n      visibility: visible;\r\n    }\r\n  }\r\n\r\n  .lineLabel{\r\n    width: 50px;\r\n    font-size: 8vh;\r\n  }\r\n}\r\n","/**\r\n * First page the user sees, the landing page.\r\n *\r\n * Contains a 3D asset in the back.\r\n */\r\n\r\n// Constants\r\n$FONT_SIZE_LINK: 6vh;\r\n$FONT_SIZE_LABEL: 4vh;\r\n$FONT_SIZE_TITLE: 8vh;\r\n\r\n.landing-page-background {\r\n  background-color: black;\r\n  width: 100%;\r\n  height: 100vh;\r\n\r\n  #landing-page-cube {\r\n    width: 100%;\r\n    height: 100vh;\r\n  }\r\n}\r\n\r\n.landing-page {\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  justify-content: center;\r\n\r\n  .link {\r\n    color: white;\r\n    text-decoration: none;\r\n    font-size: $FONT_SIZE_LINK;\r\n  }\r\n\r\n  #about {\r\n    position: absolute;\r\n    top: 25px;\r\n    left: 25px;\r\n\r\n  }\r\n\r\n  #people {\r\n    position: absolute;\r\n    bottom: 25px;\r\n    left: 25px;\r\n  }\r\n\r\n  #curr_line {\r\n    position: absolute;\r\n    font-size: 10vh;\r\n    left: 20vw;\r\n    bottom: 45%;\r\n  }\r\n\r\n  #label {\r\n    position: absolute;\r\n    font-size: $FONT_SIZE_LABEL;\r\n    right: 50px;\r\n    bottom: 50px;\r\n  }\r\n\r\n  .logo {\r\n    position: absolute;\r\n\r\n    // Gonna be totally honest I don't think this is _quite_ accurate to get 21px under the logo\r\n    top: 24px;\r\n    right: 24px;\r\n    width: 61px;\r\n    height: 10vh;\r\n  }\r\n}\r\n\r\n.title-theme {\r\n  position: absolute;\r\n  top: 24px;\r\n  font-size: $FONT_SIZE_TITLE;\r\n}"],"sourceRoot":""}]);
